<script setup>
import { ref, onMounted } from 'vue'
import axios from 'axios'
import { useRouter } from 'vue-router'

const repositories = ref([])
const router = useRouter()

const fetchRepositories = async () => {
  try {
    const response = await axios.get('https://api.github.com/users/Djhhhhhh/repos')
    repositories.value = response.data
      .filter(repo => repo.name !== 'MyPic') // 排除 MyPic 仓库
      .map(repo => ({
        name: repo.name,
        description: repo.description || '暂无描述',
        createdAt: new Date(repo.created_at).toLocaleDateString(),
        updatedAt: new Date(repo.updated_at).toLocaleDateString()
      }))
      .sort((a, b) => new Date(b.updatedAt) - new Date(a.updatedAt))
  } catch (error) {
    console.error('Error fetching repositories:', error)
  }
}

onMounted(() => {
  fetchRepositories()
})

const goToRepoDetail = (repoName) => {
  router.push({ path: `/repo/${repoName}` }) // 跳转到详情页
}
</script>

<template>
  <div class="timeline-wrapper">
    <h1 class="page-title">Djhhh' 项目时间轴</h1>
    <el-timeline>
      <el-timeline-item
        v-for="(repo, index) in repositories"
        :key="repo.name"
        :timestamp="repo.updatedAt"
        placement="top"
        :style="{ animationDelay: `${index * 0.3}s` }"
      >
        <!-- 卡片内容 -->
        <el-card class="repo-card">
          <!-- GitHub 图标 -->
          <a :href="'https://github.com/Djhhhhhh/' + repo.name" target="_blank" class="github-icon-link">
            <img src="https://upload.wikimedia.org/wikipedia/commons/9/91/Octicons-mark-github.svg" alt="GitHub" class="github-icon" />
          </a>

          <div style="font-size:x-large;margin-bottom:10px">{{ repo.name }}</div>
          <p><strong>创建时间：</strong> {{ repo.createdAt }}</p>
          <p><strong>最后修改时间：</strong> {{ repo.updatedAt }}</p>
          <p><strong>项目描述:</strong> {{ repo.description }}</p>

          <!-- 悬浮时出现的"进入"字样 -->
          <div class="enter-button" @click.stop="goToRepoDetail(repo.name)">
            了解详情
          </div>
        </el-card>
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

