// src/store/index.js
import { createStore } from 'vuex'
import axios from 'axios'

// 创建 Vuex Store
export default createStore({
  state: {
    repositories: [],
    token: process.env.VUE_APP_GITHUB_TOKEN // 从环境变量中读取 GitHub Token
  },
  getters: {
    // 获取仓库列表
    repositories (state) {
      return state.repositories
    }
  },
  mutations: {
    // 更新仓库数据
    setRepositories (state, repositories) {
      state.repositories = repositories
    }
  },
  actions: {
    // 发起 API 请求获取仓库数据
    async fetchRepositories ({ commit, state }) {
      try {
        const response = await axios.get('https://api.github.com/users/Djhhhhhh/repos', {
          headers: {
            Authorization: `Bearer ${state.token}` // 使用从环境变量中读取的 token
          }
        })

        const repositories = response.data
          .filter(repo => repo.name !== 'MyPic') // 排除 'MyPic' 仓库
          .map(repo => ({
            name: repo.name,
            description: repo.description || '暂无描述',
            createdAt: new Date(repo.created_at).toLocaleDateString(),
            updatedAt: new Date(repo.updated_at).toLocaleDateString()
          }))
          .sort((a, b) => new Date(b.updatedAt) - new Date(a.updatedAt))

        // 使用 mutation 更新仓库数据
        commit('setRepositories', repositories)
      } catch (error) {
        console.error('Error fetching repositories:', error)
      }
    }
  },
  modules: {}
})
