<template>
  <div class="repo-detail">
    <router-link to="/" class="back-button">← 返回主页</router-link>
    <a :href="githubUrl" target="_blank" class="github-link">
      <img src="https://upload.wikimedia.org/wikipedia/commons/9/91/Octicons-mark-github.svg" alt="GitHub" class="github-icon">
    </a>
    <h5 class="repo-title">{{ repoName }} 仓库的 项目介绍 </h5>
    <div class="readme-content" v-html="readmeContent"></div> <!-- 渲染 Markdown 转换后的 HTML -->
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import axios from 'axios'
import { useRoute } from 'vue-router'
import { marked } from 'marked'
import './whitey.css'

const readmeContent = ref('') // 存储 README 文件内容
const repoName = ref('') // 存储仓库名称

// 获取当前路由的参数
const route = useRoute()

// 获取仓库名称
repoName.value = route.params.name

// 加载仓库的 README 文件
const fetchReadme = async () => {
  try {
    const response = await axios.get(`https://api.github.com/repos/Djhhhhhh/${repoName.value}/readme`)
    const base64Content = response.data.content

    // 解码 Base64 内容
    const decodedContent = decodeBase64(base64Content)

    // 将 Markdown 内容转换为 HTML
    readmeContent.value = marked(decodedContent)
  } catch (error) {
    console.error('Error fetching README:', error)
    readmeContent.value = '无法加载 README 文件。'
  }
}

// 解码 Base64 内容
const decodeBase64 = (base64String) => {
  const binaryString = atob(base64String)
  const binaryLength = binaryString.length
  const bytes = new Uint8Array(binaryLength)

  // 将二进制字符串转换为 Uint8Array
  for (let i = 0; i < binaryLength; i++) {
    bytes[i] = binaryString.charCodeAt(i)
  }

  // 使用 TextDecoder 解码为文本
  const decoder = new TextDecoder('utf-8')
  return decoder.decode(bytes)
}

// 监听图片加载
onMounted(() => {
  fetchReadme()

  // 使用 MutationObserver 监听 .readme-content 的变化
  const observer = new MutationObserver(() => {
    const images = document.querySelectorAll('.readme-content img')
    images.forEach((img) => {
      // 确保每个图片都能应用样式
      img.style.maxWidth = '100%'
      img.style.height = 'auto'
      img.style.display = 'block'
      img.style.margin = '10px 0'
      img.style.borderRadius = '8px'
    })
  })

  // 开始监听 .readme-content 的 DOM 变化
  const readmeContainer = document.querySelector('.readme-content')
  observer.observe(readmeContainer, {
    childList: true, // 监听子节点变化
    subtree: true // 监听整个 DOM 树变化
  })
})
</script>

